<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/admin"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>可进行管理员的修改，用于登录平台管理，无法登录商城前台及商家管理平台</li>
        <li>管理员必须赋予角色权限，如无角色权限请移至系统->权限->角色管理中新增</li>
      </cms-prompt>
      <el-form ref="form" :rules="updateRules" :model="formData" label-width="210px">
        <el-form-item label="用户名" prop="username" :error='errorMessage.username'>
           {{formData.username}}
        </el-form-item>
        <el-form-item label="密码" prop="password"  :error='errorMessage.password' >
          <el-input v-model="formData.password" type="password" :autofocus="true" ref="first" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPassword"  :error='errorMessage.checkPassword' >
          <el-input v-model="formData.checkPassword" type="password" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="Email" prop="email" :error='errorMessage.email'>
          <el-input v-model="formData.email" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="enabled" :error='errorMessage.enabled'>
          <cms-switch v-model="formData.enabled"></cms-switch>
        </el-form-item>
        <el-form-item label="角色" prop="roleid"  :error='errorMessage.roleid'>
          <el-checkbox-group v-model="formData.roleid">
            <el-checkbox v-for="item in roleList" :key="item.id" :label="item.id">
              {{item.roleName}}
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
         <cms-button @click="update"  v-color >保存修改</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { coreRolePage } from '@/api/admin/role.js'
import { adminUpdate, adminGet, validMail } from '@/api/admin/admin.js'

var defaultData = {
  'username': '',
  'password': '',
  'checkPassword': '',
  'email': '',
  'enabled': true,
  'roleid': []
}
export default {
  name: 'adminEdit',
  mixins: [form],
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        if (this.type === 'save') {
          callback(new Error('请输入密码'))
        } else {
          callback()
        }
      } else {
        if (this.formData.password !== '') {
          this.$refs.form.validateField('checkPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        if (this.formData.password !== '') {
          callback(new Error('请再次输入密码'))
        } else {
          callback()
        }
      } else if (value !== this.formData.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var validateMail = (rule, value, callback) => {
      // 验证邮箱名的唯一性
      validMail({
        id: this.formData.id,
        validMail: this.formData.email
      }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('邮箱已存在，不允许重复绑定'))
          }
        }
      })
    }
    return {
      roleList: [],
      formData: Object.assign({}, defaultData),
      updateRules: {// 修改规则
        password: [rules.password(validatePass), rules.len(6, 16)],
        checkPassword: [rules.password(validatePass2)],
        email: [rules.email(), rules.required('邮箱必填'), rules.custom(validateMail)],
        enabled: [rules.required(null, 'blur', 'boolean')],
        roleid: [rules.required('至少勾选一个角色', 'change', 'array')]
      }

    }
  },
  methods: {
    getFormData(id) { // 通过id获取表单信息
      adminGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
        this.formData.password = ''// 强制置空。预防万一
        this.formData.roleid = res.data.roles.map(v => v.id)
      })
    },
    update() {
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          this.loading = true
          let params
          if (this.formData.password !== '') {
            params = Object.assign({}, this.formData)
            params.password = this.$cms.encrypt(params.password)
            delete params.checkPassword
          } else {
            params = Object.assign({}, this.formData)
            params.password = ''
            delete params.checkPassword
          }
          adminUpdate(params).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
              this.loading = false
              this.rest()
              this.$routerLink('/admin/list', 'list')
            } else {
              this.$errorMessage(res.message)
              this.loading = false
            }
          })
        }
      })
    },
    mockData() { // 开发模式可以使用这个添加测试数据
      return false
    },
    test() {
      this.timer = setInterval(this.mockData, 600)
    },
    getRoleList() { // 获取角色列表
      coreRolePage({ size: 999999, page: 0 }).then(res => {
        if (res.code === 200) {
          this.roleList = res.data.content
        }
      })
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.formData.checkPassword = ''
      this.autoFocus()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
    this.getRoleList()
  }
}
</script>




